import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import {resources, nameSpaces } from "./resources";
i18n.use(initReactI18next).init({
  fallbackLng: 'en',
  lng: 'en',
  resources,
  ns: nameSpaces,
  defaultNS: nameSpaces[0]
});

i18n.languages = ['en', 'hn'];

export default i18n;