import React from 'react';
import { Container, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import { withTranslation } from 'react-i18next';

import logo from '../../Layouts/Home/images/LOGO.webp';
import './Navbar.css';
import i18n from "../../i18n/config";
import history from '../../history';


function Navmenu(props) {
  const handleSelection = (selectedProduct) => {
    const language = i18n.language;
    history.push(`/${language}/${selectedProduct}`)
  }

  const{t}=props;

  return (
    //<div className="container">
    //<div className="Navmenu">
    <Container>
      <Navbar variant="light" sticky="top" expand="md" collapseOnSelect>
        <Navbar.Brand>
          <a href="/">
            <img src={logo} width="240vw" height="auto" className='logo-img' /></a>{' '}
        </Navbar.Brand>
        <Navbar.Toggle className="coloring" />
        <Navbar.Collapse className="right-aligned">
          <Nav>
            <NavDropdown title={t('CTAFooter:headerName6')}>
              <NavDropdown.Item onClick={() => handleSelection("mutual-funds")}>{t('CTAFooter:headerName7')}</NavDropdown.Item>
              <NavDropdown.Item onClick={() => handleSelection("fixed-deposit")}>{t('CTAFooter:headerName8')}</NavDropdown.Item>
              <NavDropdown.Item onClick={() => handleSelection("pms")}>{t('CTAFooter:headerName9')}</NavDropdown.Item>
              <NavDropdown.Item onClick={() => handleSelection("peer-to-peer-lending")}>{t('CTAFooter:headerName10')}</NavDropdown.Item>
              <NavDropdown.Item onClick={() => handleSelection("alternative-investment-fund")}>{t('CTAFooter:headerName11')}</NavDropdown.Item>
              <NavDropdown.Item onClick={() => handleSelection("national-pension-scheme")}>{t('CTAFooter:headerName12')}</NavDropdown.Item>
              <NavDropdown.Item onClick={() => handleSelection("bond")}>{t('CTAFooter:headerName13')}</NavDropdown.Item>
              <NavDropdown.Item onClick={() => handleSelection("gold")}>{t('CTAFooter:headerName14')}</NavDropdown.Item>
              <NavDropdown title={t('CTAFooter:headerName15')} className='loans-menu'>
                <NavDropdown.Item onClick={() => handleSelection("loans/instant-home-loan")}>{t('CTAFooter:headerName17')}</NavDropdown.Item>
                <NavDropdown.Item onClick={() => handleSelection("loans/loan-against-securities")}>{t('CTAFooter:headerName16')}</NavDropdown.Item>
              </NavDropdown>
            </NavDropdown>
            <NavDropdown title={t('CTAFooter:headerName1')}>
              <NavDropdown.Item href="/partner">{t('CTAFooter:footerLink25')}</NavDropdown.Item>
              <NavDropdown.Item href="/partner/become-mutual-fund-distributors">{t('CTAFooter:headerName3')}</NavDropdown.Item>
              <NavDropdown.Item href="/partner/grow-your-mutual-fund">{t('CTAFooter:headerName4')}</NavDropdown.Item>
              <NavDropdown.Item href="/partner/all-about-amfi-arn-code">{t('CTAFooter:footerLink29')}</NavDropdown.Item>
            </NavDropdown>
            <a href="https://app.nivesh.com/home/index" className="register-button">Login/Register</a>
            <NavDropdown title={i18n.language === 'en' ? 'English' : 'Hindi'} className='language-selector'>
              <NavDropdown.Item onClick={() => props.changeLanguage('en')} >English
              </NavDropdown.Item>
              <NavDropdown.Item onClick={() => props.changeLanguage('hn')} >Hindi</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </Container>
    // </div>
    // </div>
  );
}

export default withTranslation()(Navmenu)